@charset "utf-8";
@import url('//cdn.rawgit.com/innks/NanumSquareRound/master/nanumsquareround.min.css');

/* reset */

* {margin:0; padding:0; border:0; outline:0; box-sizing:border-box; color:#000;
-webkit-tap-highlight-color:none;}

body, html {font-family: 'NanumSquareRound',sans-serif; min-height:100%; background: #f7f7f7;}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {margin:0; padding:0; border:0; font-size:100%; vertical-align:baseline;}

h1, h2, h3, h4, h5, h6 {font-size:100%; letter-spacing:normal;}

fieldset, img, abbr,acronym {border:0 none;}

table {border-collapse:separate; border-spacing:0; border:0 none;}
caption, th, td {text-align:left; font-weight:normal; border:0;}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display:block;}
img, video {width:100%; border:0;}
audio, canvas, video {display:inline-block; display:inline; zoom:1;}
img {image-rendering:-moz-auto;/*firefox*/
image-rendering:-o-auto;/*opera*/
image-rendering:-webkit-optimize-contrast;/*webkit*/
image-rendering:auto;
-ms-interpolation-mode:nearest-neighbor; -webkit-perspective:1;}
ul, ol, dl {list-style:none;}
a {display:block; text-decoration:none; color:inherit;}
i {display:block; font-style:normal; font-weight:normal;}

table {border-collapse:collapse; border-spacing:0;}
input, select, label {vertical-align:middle;}
button, input, select, textarea {font-size:100%; margin:0; vertical-align:baseline; *vertical-align:middle;font-weight:normal;letter-spacing:normal; background: none; font-family: 'NanumSquareRound',sans-serif;}
button, input {line-height:normal; box-shadow: none;}
input {-webkit-appearance:none; -webkit-border-radius:0;}
input::-ms-clear,
input::-ms-reveal {display:none;width:0;height:0;}
input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {display:none;}
button, select {text-transform:none;}
button::-moz-focus-inner, input::-moz-focus-inner {border:0; padding:0;}
textarea {overflow:auto; resize:none}
address{font-style:normal;}
.clear::after{content:""; display:block; clear:both;}

/* swiper */
/* MainBanner */
.MainBannerWrap .swiper-slide {margin: calc(100vw*(8/414)) 0;}
.MainBannerWrap .swiper-pagination-bullet {background: #ED2939; opacity: 0.35;}
.MainBannerWrap .swiper-pagination-bullet-active {background: #ed2939; opacity: 1;}
.MainBannerWrap .swiper-container {padding-bottom: calc(100vw*(25/414))}
.MainBannerWrap .swiper-container-horizontal > .swiper-pagination-bullets {bottom: calc(100vw*(10/414));}
.MainBannerWrap .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {width: calc(100vw*(8/414)); height: calc(100vw*(8/414)); margin: 0 calc(100vw*(10/414));}

/* MarketDeal */
.MarketBannerWrap .swiper-container {padding: 0 calc(100vw*(10/414));}

/* MainRecentPurchases */
.PurchaseWrap .swiper-container {padding: 0 calc(100vw*(10/414));}


